* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  clear: both;
}

:root {
  --primary-color: #3498db;
  --secondary-color: #2ecc71;
  --paragraph-color: #333333;
  --link-color: $primary-color;
  --button-color: $primary-color;
  --danger-color: #e74c3c;
  --alert-color: #f39c12;
  --success-color: #2ecc71;
  --warning-color: #f39c12;
  --black-color: #000000;
  --white-color: #ffffff;
}

